//Module import
import * as THREE from 'three';
import * as PIXI from 'pixi.js';
/**
 * A wrapper around THREE and PIXI rendering engines to give them the same syntax 
 * to handle with.
 */
interface Graphics {
    id: number;
    domObject: HTMLCanvasElement;
}

class Graphics3D implements Graphics {
    domObject: HTMLCanvasElement;
    constructor(public id = 0){
        this.domObject = (new THREE.WebGLRenderer()).domElement;
    }
}

class Graphics2D implements Graphics {
    domObject: HTMLCanvasElement;
    constructor(public id = 0){
        this.domObject = (new PIXI.Application()).view;
    }
}

let graphics3 = new Graphics3D(0);
let graphics2 = new Graphics2D(1);
console.log(graphics2.domObject);
console.log(graphics3.domObject);